import Vue from 'vue'
import Router from 'vue-router'
import testA from '@/components/testA'
import home from '@/components/home'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/testA',
      name: 'testA',
      component: testA
    }
  ]
})



